import React from 'react'
import {WingBlank,Carousel} from 'antd-mobile'
import './tb.css'
import Mytab from './Mytab';

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
class Home extends React.Component {

    constructor(props){
        super(props);
        this.state={
            data:['1','2','3'],  
            imgHeight: 176, 
            data1 : [{
              src:'https://img.alicdn.com/imgextra/i4/55815106/O1CN01uNl1J31naawFdgd8m_!!0-saturn_solar.jpg_220x220.jpg_.webp',
              name:'黑麦威士忌',
              price:'￥111',
              number:'100人购买',
            },
                {src:'https://img.alicdn.com/imgextra/i2/118669861/O1CN0121jFBu2MiOE7tfsYD_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'波本酒',
                price:'￥222',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i4/291340029/O1CN01VAP52Q1C5K6z7sGyN_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'基尔酒',
                price:'￥333',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i4/55538386/O1CN0134kVaL2Boq0le0Ei6_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'朗姆酒',
                price:'￥444',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i1/55538386/O1CN0119YOke2Boq2Rivk2B_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'伏特加',
                price:'￥555',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i2/118669861/O1CN0121jFBu2MiOE7tfsYD_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'琴酒',
                price:'￥666',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i3/291340029/O1CN01YrekGQ1C5K2WmJFvg_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'雪莉酒',
                price:'￥777',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i3/113296325/O1CN01G0rlPh1watjqy1T0Z_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'龙舌兰',
                price:'￥888',
                number:'100人购买', 
              },
                {src:'https://img.alicdn.com/imgextra/i3/59422206/O1CN01VDD8iZ1SAOLrpIXkf_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'苦艾酒',
                price:'￥999',
                number:'100人购买',
              },
                {src:'https://img.alicdn.com/imgextra/i2/169530060/O1CN01Ro15O41CJWOW2Dd40_!!0-saturn_solar.jpg_220x220.jpg_.webp',
                name:'基安蒂',
                price:'￥1000',
                number:'100人购买',
              }
    ]
        }
    }
    componentDidMount() {
        // simulate img loading
        setTimeout(() => {
          this.setState({
            data: ['https://aecpm.alicdn.com/simba/img/TB12NW9kH1YBuNjSszhSuwUsFXa.jpg', 
            'https://img.alicdn.com/imgextra/i1/6000000000720/O1CN01QON2dX1HBnnbupK0g_!!6000000000720-0-octopus.jpg', 
            'https://img.alicdn.com/imgextra/i4/6000000002436/O1CN01r0sGKc1TrjRkVM8w8_!!6000000002436-0-octopus.jpg'],
          });
        }, 100);
      }
    render() {
    return (
        <div>
             <header className="Search">
                    <div className="header-bd">
                        <a className="placeholder" onClick={()=>{this.props.history.push('/home/search')}}>
                        <span className="text iconfont iconsousuo">寻找宝贝店铺</span>
                        </a>
                    </div>
                </header>
        <Carousel
          autoplay={true}
          infinite
          style={{marginTop:'37px'}}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={val}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
                <ul className='goods'>
               
                    {
                        this.state.data1.map((data,i)=>(
                            <li key={i} 
                                onClick={()=>this.props.history.push({
                                    pathname:'/home/child',
                                    // id:i,
                                    src:data.src
                              })}
                              className='details'
                            >
                                <img src={data.src}/>
                                <p className='p1'>{data.name}</p>                              
                                <p className='p2'>{data.price}</p>
                                <p className='p3'>{data.number}</p>
                               
                            </li>
                        ))
                    }
                </ul>
        
        <Mytab/>
           
            
        </div>
    )
}
}
export default Home
