import React from 'react'
import {
   
    withRouter
} from 'react-router-dom'
import {NavBar,Icon,TabBar,Button} from 'antd-mobile'
const Child =(props)=> {
        return (
            <div className='doc'>
                <Icon type='left' size='lg' onClick={()=>props.history.goBack()}/>
                <div>
                    <img src={props.location.src}/>
                </div> 

                <div className="bottomNav" style={ {position: 'fixed',bottom:0, width: '100%',height:'50px',lineHeight:'50px'}}>
                  <div className='btns'>
                    <button style={{width:'16%',backgroundColor:'white',border:'none'}} >
                        <i className='iconfont icon-dianpu'></i>
                        店铺
                    </button>
                    <button style={{width:'16%',backgroundColor:'white',border:'none'}}>
                        <i className='iconfont icon-kefu' ></i>
                        客服
                    </button>
                    <button style={{width:'16%',backgroundColor:'white',border:'none'}}>
                        <i className='iconfont icon-shoucang'></i>
                        收藏
                    </button>
                    <button  style={{width:'26%',backgroundColor:' rgb(255, 133, 42)',border:'none'}}>
                        加入购物车
                    </button>
                    <button  style={{width:'26%',backgroundColor:' rgb(255, 133, 42)',border:'none'}}>
                        立即购买
                    </button>
                  </div>
                </div>
                
            </div>
        )
    
      }
    
    
export default withRouter(Child)