import React from 'react';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'

import Mytab from './Mytab';
import Home from './Home';
import Cart from './Cart';
import Child from './Child';
import Code from './Code';
import Mine from './Mine';
import Search from './Search'
// https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg
const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/home' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/home/detail' component={Cart} />
			<Route path='/home/child' render={(props)=><Child {...props} />}/>
            <Route path='/home/search' component={Search}/>
            <Route path='/code' component={Code}/>
            <Route path='/Mine' component={Mine}/>

			<Route component={Home} />
			{/* <Route render={()=><Redirect to='/'/>} /> */}
		</Switch>
		{/* <Mytab /> */}
	</Router>
}
export default App;