import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        if(pathname.split('/').length>2){
            return null;
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-icon4'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon4'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-weibiaoti-'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-weibiaoti-'></i>
                           
                        }
                        title="订单列表"
                        key="code"
                        selected={ pathname === '/code' }
                        onPress={ () => {
                            this.props.history.push('/code')

                        } }
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode2'></i> }
                        selectedIcon={<i className='iconfont icon-wode2'></i>  }
                        title="我的淘宝"
                        key="mine"
                        selected={ pathname === '/mine' }
                        onPress={ () => {
                            this.props.history.push('/mine')
                        } }
                    >
                        
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);